.wrapper {
  margin-bottom: 20px; }

.photoUploader {
  width: 100%;
  max-width: 252px;
  height: 252px;
  margin-bottom: 15px; }

.photoUploader .input {
    display: none; }

.photoUploader .input + label {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -ms-flex-line-pack: center;
          align-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      font-size: 16px;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      width: inherit;
      height: inherit;
      background-color: rgba(0, 0, 0, 0.2);
      -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);
      box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);
      border: dashed 1px #fff;
      color: #209e91;
      cursor: pointer; }

.image-previewer {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.imgContainer {
  width: 100px;
  height: 100px;
  margin-right: 15px;
  margin-bottom: 15px;
  position: relative;
  background: url("/img/transparent-bg.png") center no-repeat;
  background-size: cover; }

.imgContainer img {
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
       object-fit: contain; }

.imgCover {
  position: absolute;
  top: 0px;
  left: 0;
  width: inherit;
  height: inherit;
  border: 3px solid #209e91;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.imgHover {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  opacity: 0;
  visibility: hidden;
  top: 0px;
  width: inherit;
  height: inherit;
  text-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: rgba(0, 0, 0, 0.64);
  -webkit-transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
       -o-transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
          transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-delay: 0s;
       -o-transition-delay: 0s;
          transition-delay: 0s; }

.imgHover .remove-img {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px;
    height: auto;
    color: #fff; }

.imgHover label {
    padding: 0 5px; }

.imgHover label i {
      margin-right: 5px;
      font-size: 18px;
      width: 18px; }

.imgContainer:hover .imgHover {
  visibility: visible;
  opacity: 1 !important; }

.lablePhotoUploader {
  text-align: center; }

.lablePhotoUploader img {
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
       object-fit: contain; }
