.buttonWrapper {
  display: inline-block; }

.button {
  background: transparent;
  color: #fff;
  font-size: 18px;
  border: 0;
  vertical-align: bottom;
  height: 34px;
  width: 36px;
  border-radius: 0;
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.button svg {
  fill: #fff;
  width: 20px;
  height: 20px; }

.button:hover, .button:focus {
  background-color: rgba(111, 111, 111, 0.075);
  outline: 0;
  /* reset for :focus */ }

.active {
  color: #6a9cc9; }

.active svg {
  fill: #6a9cc9; }
