.uploadButton {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 16px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: inherit;
  height: inherit;
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);
  border: dashed 1px #fff;
  color: #209e91;
  cursor: pointer;
  width: 100%;
  height: 256px;
  margin-bottom: 20px; }

.product-images-preview {
  margin: 0 -7px;
  width: 100%; }

.product-images-preview .imageItem {
    width: 130px;
    height: 130px;
    display: inline-block;
    margin: 0 7px 14px 7px;
    position: relative;
    border: 4px solid transparent; }

.product-images-preview .imageItem.isCover {
      border: 4px solid #209e91; }

.product-images-preview .imageItem img {
      width: 100%;
      height: 100%;
      -o-object-fit: cover;
         object-fit: cover; }

.product-images-preview .imageItem .imageHover {
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.75);
      top: 0;
      left: 0;
      opacity: 0;
      -webkit-transition: all 0.1s;
      -o-transition: all 0.1s;
      transition: all 0.1s; }

.product-images-preview .imageItem .imageHover .removeImageButton {
        position: absolute;
        top: 0;
        right: 0; }

.product-images-preview .imageItem .imageHover .removeImageButton:hover {
          background-color: #fff; }

.product-images-preview .imageItem .imageHover .removeImageButton:hover i {
            color: #000; }

.product-images-preview .imageItem .imageHover .removeImageButton i {
          color: #fff; }

.product-images-preview .imageItem .imageHover .makeCoverButton {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        color: #fff; }

.product-images-preview .imageItem .imageHover .makeCoverButton:hover {
          background-color: #fff;
          color: #000; }

.product-images-preview .imageItem:hover .imageHover {
      opacity: 1; }
